import { Link, useStaticQuery } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import Img from "gatsby-image"
import Wallpaper from "../images/ferdinand-stohr-NFs6dRTBgaM-unsplash.jpg"
import BackgroundVideo from "../images/in-the-deep.mp4"
import "./home.css"
import PageWidth from "./pagewidth"

const Home = ({ siteTitle }) => {

  const data = useStaticQuery(graphql`
    query {
      placeholderImage: file(relativePath: { eq: "jeremy/portrait-close.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 200) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  return (
  <section className="home-intro-section">
    <div className="video-background">
      <video autoPlay={true} muted={true} loop={true} id="myVideo">
        <source src={BackgroundVideo} type="video/mp4" />
      </video>
      <span className="home-content">
        <PageWidth>
          <Img className="portrait hoverable" fluid={data.placeholderImage.childImageSharp.fluid} />
          <h1>Hi, I'm <strong className="header-name" title="Hey. Thank you for visiting!">Jeremy Bernier!</strong></h1>
          {/*<div className="grid">*/}
          <div className="home-intro">
            <p className="home-intro-subtext">I'm a <strong>Senior Software Engineer</strong> specializing in Web App Development with React and Node.js (<a href="/resume.pdf">resume</a>)</p>
            <p><a href="mailto:
    &#106;&#101;&#114;&#101;&#109;&#121;&#046;&#115;&#046;&#098;&#101;&#114;&#110;&#105;&#101;&#114;&#064;&#103;&#109;&#097;&#105;&#108;&#046;&#099;&#111;&#109;" title="Email is the easiest">Email me</a> if you're interested in working together (remote only)</p>

            {/*<p>Sign up to my email list! <input type="text" /></p>*/}
          </div>
          {/*</div>*/}
        </PageWidth>
      </span>
    </div>
  </section>
)}

Home.propTypes = {
}

Home.defaultProps = {
}

export default Home
