const Portfolio = ({ limit }) => {
return (
  <StaticQuery
    query={graphql`
      query GetBlogPosts {
        site {
          siteMetadata {
            title
            description
          }
        }
        allMarkdownRemark(
          sort: { fields: [frontmatter___date], order: DESC }
        ) {
          edges {
            node {
              excerpt
              fields {
                slug
              }
              frontmatter {
                date(formatString: "DD MMMM, YYYY")
                title
                type
                type
                cover {
                  publicURL
                    childImageSharp {
                      sizes(maxWidth: 2000) {
                        ...GatsbyImageSharpSizes
                      }
                    }
                }
              }
            }
          }
        }
      }
    `}
    render={data => {
      const finalData = data.allMarkdownRemark.edges
        .filter(({ node }) => !node.fields.slug.startsWith('/projects'))
        .filter(({ node }) => {
          const { type } = node.frontmatter
          if (type === 'unlisted' || type === 'draft') {
            return false
          }
          return true
        })
        
      return (
        <div>
          <section
            style={{
              paddingTop: '.7rem',
              marginBottom: `1.45rem`,
            }}
          >
            <div
              style={{
                margin: `0 auto`,
                maxWidth: 960,
                padding: `.3rem 1.0875rem`,
              }}
            >
            <Tiles data={finalData} limit={limit} />
            {limit != null && <Link to="/blog" className="view-more-blog-text">View more blog posts</Link>}
            </div>
          </section>
        </div>
      )}}
  />
)}

Portfolio.propTypes = {
}

Portfolio.defaultProps = {
}

export default Portfolio
